<template>
  <div class="header">
    我的全部购物车({{ cartCount }})
  </div>
  <div class="wrapper">
    <div class="cart">
      <template
      v-for="(item, index) in cartList"
      :key="item.shopName">
        <ProductList
          class="product__list__item"
          v-if=(Object.keys(item.productList).length)
          :productList="item.productList"
          :shopName="item.shopName"
          @click="handleCartClick(index)"
        />
      </template>
    </div>

  </div>
  <Docker :currentIndex="1"/>
</template>

<script>
import { computed } from 'vue'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'
import Docker from '../../components/Docker.vue'
import ProductList from '../..//components/ProductList.vue'
export default {
  name: 'CartListView',
  components: { Docker, ProductList },
  setup () {
    const store = useStore()
    const router = useRouter()
    const handleCartClick = (index) => {
      router.push({ name: 'OrderConfirmationView', params: { shopId: index } })
    }
    const cartList = store.state.cartList
    console.log(cartList)
    const cartCount = computed(() => {
      return Object.keys(cartList).length
    })
    return { cartList, cartCount, handleCartClick }
  }
}
</script>

<style lang="scss" scoped>
@import "../../style/virables.scss";
@import "../../style/mixins.scss";
.header{
  position: relative;
  top:0;
  right:0;
  left:0;
  height: .64rem;
  background: $bgColor;
  text-align: center;
  line-height: .64rem;
  font-size: .16rem;
  color: $content-fontColor;
}
.wrapper{
  position: absolute;
  left: 0;
  top: .64rem;
  right: 0;
  bottom: .49rem;
  background-color: #eee;
  overflow-y: scroll;
}
.product__list__item{
  cursor: pointer;
}
</style>
